<template lang="pug">
  .link-zone
    .link
      router-link(to="/test") test
    .link
      router-link(to="/components") components
</template>

<script>

import { store } from '../vuex/store';

export default {
  name: 'HelloWorld',
  store,
  data() {
    return {
      site: '',
      msg: 'Welcome to Hanu Vue Template',
      posX: 0,
      posY: 0
    };
  },
  computed: {
    count() {
      return this.$store.state.count;
    },
    getPosition() {
      return {
        left: `${this.posX}px`,
        top: `${this.posY}px`
      };
    }
  },
  methods: {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass">
  .link-zone
    width: 100vw
    height: 100vh
    .link
      height: 40px
      text-align: center
      line-height: 40px
      border: solid 1px #eeeeee
</style>



