<template lang="pug">
  .loadingWindow(v-if="loadingState")
    .boxLoading
</template>
<script>
export default {
  name: 'my-small-progress',
  computed: {},
  data() {
    return {
      loadingState: false
    };
  },
  methods: {
    start() {
      this.loadingState = true;
    },
    end() {
      this.loadingState = false;
    }
  }
};
</script>
<style scoped lang="sass">
.loadingWindow
  width: 100%
  height: 100%
  position: relative
  z-index: 9999
  background: white
.boxLoading
  width: 50px
  height: 50px
  margin: auto
  position: absolute
  left: 0
  right: 0
  top: 0
  bottom: 0
  &:before
    content: ''
    width: 50px
    height: 5px
    background: #000
    opacity: 0.1
    position: absolute
    top: 59px
    left: 0
    border-radius: 50%
    animation: shadow .5s linear infinite
  &:after
    content: ''
    width: 50px
    height: 50px
    background: #1A6844
    animation: animate .5s linear infinite
    position: absolute
    top: 0
    left: 0
    border-radius: 3px

@keyframes animate
  17%
    border-bottom-right-radius: 3px
  25%
    transform: translateY(9px) rotate(22.5deg)
  50%
    transform: translateY(18px) scale(1, .9) rotate(45deg)
    border-bottom-right-radius: 40px
  75%
    transform: translateY(9px) rotate(67.5deg)
  100%
    transform: translateY(0) rotate(90deg)

@keyframes shadow
  0%, 100%
    transform: scale(1, 1)
  50%
    transform: scale(1.2, 1)

body, html
  background: #ccc
  overflow: hidden
  height: 100%
</style>



